  font-size: 1rem;
  opacity: 1;
  display: block;
}
.sidebar.close .nav-links li:hover .sub-menu {
  top: 0;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.3s ease;
}

.sidebar .nav-links .showMenu {
  background-color: #eee;
}

.sidebar .nav-links li .sub-menu.blank {
  opacity: 1;
  pointer-events: auto;
  padding: 0 20px 0 16px;
  opacity: 0;
